window.jQuery = function(selectorOrArray){

    let elements
    if(typeof selectorOrArray === 'string')
    {
        elements = document.querySelectorAll(selectorOrArray)
    }else  if(selectorOrArray instanceof Array){
        elements = selectorOrArray
    }
    //apiÂèØ‰ª•Êìç‰Ωúelements
    /*const api  */ return {
        OldApi: selectorOrArray.OldApi,
        addClass(className){ // = addClass: function(className)
            //Èó≠ÂåÖ;ÂáΩÊï∞ËÆøÈóÆÂ§ñÈÉ®ÂèòÈáè
            for(let i = 0; i < elements.length;i ++)
            {
                elements[i].classList.add(className)
            }
            return this;//‰πüÂèØ‰ª•return api 
        },

        find(selector){
            let array = []
            for(let i = 0;i <elements.length;i++){
                array = array.concat(Array.from(elements[i].querySelectorAll(selector)))
            }
            array.OldApi = this // thisÊòØÊóßÁöÑapi
            return jQuery(array)
        },
        OldApi: selectorOrArray.OldApi,
        end(){
            return this.OldApi;//Êñ∞ÁöÑapiËøîÂõûÊóßÁöÑapi
        },
        each(fn){
            for(let i = 0;i<elements.length;i++)
            {
                fn.call(null,elements[i],i)
            }
            return this
        },
        
        parent(){
            const array = []
            this.each((node)=>{
                if(array.indexOf(node.parentNode) === -1)
                {
                    array.push(node.parentNode)
                }
            })
            return jQuery(array)
        },

        print(){
            console.log(elements)
        },

        children(){
            const array = []
            this.each((node)=>{
                   return array.push(...node.children)// ...ÊääÁªìÊûÑÊï∞ÁªÑÊãÜÂºÄÊîæ‰∏Ä‰∏™Êï∞ÁªÑ
            })
            return jQuery(array)
        },  

    }
} 

window.$ = window.jQuery